import React, { Component } from 'react';
import { Row, Col, Container } from 'react-bootstrap'
import businessm from "../../assets/images/iti-business.jpeg";
import Designthinking from "../../assets/images/iti-designthinking.jpg";
import businessanalysis from "../../assets/images/iti-businessanalysi.jpeg";
import Internship from "../../assets/images/iti-internship.jpg";
import { Link } from 'react-router-dom'

/**
 * 
 * @author Rohit Autade
 * 
 */

class Businessanalyasis extends Component {
  state = {}
  /**
* 
* @usage this function render Business Analysis Course Component.
* 
*/
  render() {
    return (
      <>

        <br /><Container>
          <br></br>
          <h3 className="text-center">Business Analysis Courses</h3>
          <br />
          <br />
          <Row className="text-center">
            <Col>
              <Link to="Businessmanagement"> <img src={businessm} className="iti-rcorners3" alt=""></img><br></br>
                <h6 className="text-center">Business Management</h6></Link>
            </Col>
            <Col>
              <img src={Designthinking} className="iti-rcorners3" alt=""></img><br></br>
              <h6 className="text-center">Design Thinking</h6>
            </Col>
            <Col>
             <Link to="gbusiness"> <img src={businessanalysis} className="iti-rcorners3" alt=""></img><br></br>
              <h6 className="text-center">Business Analysis</h6></Link>
            </Col>

          </Row>
          <Row className="text-center">
            <Col>
              <img src={Internship} className="iti-rcorners3" alt=""></img><br></br>
              <h6 className="text-center">Company Internship</h6>
            </Col></Row>
        </Container>
      </>
    );
  }
}

export default Businessanalyasis;