import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import AI1 from "../../assets/images/iti-Ai1.png";
import AI2 from "../../assets/images/icon_bigdataAI.png";
import AI3 from "../../assets/images/icon_notebook04.png";

import AIB from "../../assets/images/iti-aifuture.jpeg";
