import React, { Component } from 'react'
import { Navbar, Nav, Form, Button, Container, NavDropdown } from 'react-bootstrap';
import { Modal, ButtonToolbar } from 'react-bootstrap';
import ItiLogo from "../../assets/images/iti-logo.JPG";
import { Formik, Field, ErrorMessage } from 'formik';
import {Link} from 'react-router-dom';

/**
 * 
 * @author Dinesh Kabade
 * 
 */
export default class Header extends Component {

    constructor(props) {
        super(props)
        this.state = {
            showLoginModal: false
        }
    }

    /**
    * 
    * @usage this function render header contents
    * 
    */
    renderHeader = () => {
        return (
            <div>
                <Container>
                    <Navbar collapseOnSelect expand="lg">
                        <Navbar.Brand><Link to="/">
                            <img src={ItiLogo} alt="First slide" className="iti-logo" /></Link>
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
                            <Nav>
                            <Nav.Link>About Us</Nav.Link>
                            <Nav.Link>Technical Services</Nav.Link>
                                {/* <Nav.Link href="#link">POC/ R and D</Nav.Link> */}
                                <NavDropdown title="Training">
                                <NavDropdown.Item>Features Of Training</NavDropdown.Item>
                                    <NavDropdown.Item><Link to="Globaltraining">Global Training Program</Link></NavDropdown.Item>
                                    <NavDropdown.Item><Link to="Japanesetraining">Japanese Language Training</Link></NavDropdown.Item>
                                </NavDropdown>
                                <Nav.Link>Contact Us</Nav.Link>
                            </Nav>
                            
                            <Form inline>
                                <Button variant="primary" onClick={() => this.handleShowLoginModal()} className="iti-desktop-login-btn">Login</Button>
                            </Form>
                        </Navbar.Collapse>
                    </Navbar>
                </Container>
            </div >
        );
    }

    /**
     * 
     * @usage this function show the login modal
     * 
     */
    handleShowLoginModal = () => {
        this.setState({
            showLoginModal: true
        })
    }

    /**
     * 
     * @usage this function close the login modal
     * 
     */
    handleCloseLoginModal = () => {
        this.setState({
            showLoginModal: false
        })
    }

    /**
     * 
     * @usage this function render login modal
     */
    renderLoginModal = () => {
        const emailTest = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;
        return (
            <ButtonToolbar>
                <Modal
                    size="lg"
                    show={this.state.showLoginModal}
                    onHide={() => this.handleCloseLoginModal()}
                    aria-labelledby="example-modal-sizes-title-lg"
                >
                    <Modal.Header closeButton>

                    </Modal.Header>
                    <Modal.Body>
                        <div className="container">
                            <div className="row mb-5">
                                <div className="col-lg-12 text-center">
                                    <h1 className="mt-5"> Welcome to IT Innovation India</h1>
                                    <h6>Login Your Self Learning Section</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-lg-12">
                                    <Formik
                                        initialValues={{ email: "", password: "" }}
                                        validate={values => {
                                            let errors = {};
                                            if (values.email === "") {
                                                errors.email = "Email is required";
                                            } else if (!emailTest.test(values.email)) {
                                                errors.email = "Invalid email address format";
                                            }
                                            if (values.password === "") {
                                                errors.password = "Password is required";
                                            } else if (values.password.length < 3) {
                                                errors.password = "Password must be 3 characters at minimum";
                                            }
                                            return errors;
                                        }}
                                        onSubmit={({ setSubmitting }) => {
                                            alert("Form is validated! Submitting the form...");
                                            setSubmitting(false);
                                        }}
                                    >
                                        {({ touched, errors, isSubmitting }) => (
                                            <Form>
                                                <div className="form-group">
                                                    <label htmlFor="email">E-mail:</label>
                                                    <Field
                                                        type="email"
                                                        name="email"
                                                        placeholder="Please Enter Your E-mail Address"
                                                        className={`form-control ${
                                                            touched.email && errors.email ? "is-invalid" : ""
                                                            }`}
                                                    />
                                                    <ErrorMessage
                                                        component="div"
                                                        name="email"
                                                        className="invalid-feedback"
                                                    />
                                                </div>

                                                <div className="form-group">
                                                    <label htmlFor="password">Password:</label>
                                                    <Field
                                                        type="password"
                                                        name="password"
                                                        placeholder="Please Enter Your Password"
                                                        className={`form-control ${
                                                            touched.password && errors.password ? "is-invalid" : ""
                                                            }`}
                                                    />
                                                    <ErrorMessage
                                                        component="div"
                                                        name="password"
                                                        className="invalid-feedback"
                                                    />
                                                </div>

                                                <button
                                                    type="submit"
                                                    className="btn btn-primary btn-block"
                                                    disabled={isSubmitting}
                                                >
                                                    {isSubmitting ? "Please wait..." : "Submit"}
                                                </button>
                                            </Form>
                                        )}
                                    </Formik>
                                </div>
                            </div>
                        </div>
                    </Modal.Body>
                </Modal>
            </ButtonToolbar>
        );
    }

    render() {
        return (
            <div>
                {this.renderHeader()}
                {this.renderLoginModal()}
            </div>
        )
    }
}
