import React, { Component } from 'react';
import { Container,Row,Col} from 'react-bootstrap';
import businesswork from "../../assets/images/iti-businesswork.jpg";
import culture from "../../assets/images/iti-cultural.jpg";
import intro from "../../assets/images/iti-introductory.jpg";
import intensive from "../../assets/images/iti-intensive.jpg";


/**
 * 
 * @author Rohit Autade
 * 
 */

class Japanesetraining extends Component {
    state = {  }
     /**
  * 
  * @usage this function render Japanese training page in training menuitem.
  * 
  */
    render() { 
        return (  
        <>
        <br/>
        <Container>
            <br/>
            <br/>
            <h2 className="text-center iti-font">Japanese Language And Business Culture Course</h2>
            <br/>
            <br/>
                 <div>
                 <Row className="text-center">
                    <Col>
                    <img src={intensive}  className="iti-rcorners3" alt=""></img><br></br>
                  <h6 className="text-center">Intensive Course</h6>
                  </Col>
                  <Col>
                  <img src={intro}  className="iti-rcorners3" alt=""></img><br></br>
                  <h6 className="text-center">Introductory Course</h6>
                  </Col>
                <Col>
                <img src={culture}  className="iti-rcorners3" alt=""></img><br></br>
                  <h6 className="text-center">Cultural workshop</h6>
                </Col>
              
                  </Row>  
                  <Row className="text-center">
                  <Col>
                <img src={businesswork}  className="iti-rcorners3" alt=""></img><br></br>
                  <h6 className="text-center">Business Workshop</h6>
                </Col>
                    </Row> 
                 </div>
        </Container>
        </>
        );
    }
}
 
export default Japanesetraining;